$text_color: #333;
$corner_radius: 4px;
$menu_text_color: darkslategray;
$title_color: teal;

html {
    overflow-y: scroll;
}

body {
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
}

a {
  color: $menu_text_color;
  background: transparent;
  word-wrap: break-word;
  word-break: break-all;
  text-decoration: none;
}

a:active, a:hover {
    color: tomato;
}

.notebooks ul {
    padding-inline-start: 0;
    list-style-type: none;
}

.list {
    max-width: 100%;
}

.list ul {
    list-style-type: none;
}

.list li {
    padding-bottom: 0.5em;
}

h1, h2{
    color: $title_color;
    font-family: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
    text-align: left;
}

h3, h4, h5, h6{
    color: $title_color;
    font-family: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
    text-align: left;
    margin-top: 0.2em;
    margin-bottom: 0.2em;
}

nav {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0%);
    font-family: monospace;
}

nav ul {
    display: flex;
    font-size: 18px;
    list-style-type: none;
    line-height: 1em;
}

nav li {
    min-width: 200px;
    float: left;
}

nav a {
    padding: $corner_radius;
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    display: block;
    text-align: center;
    color: $menu_text_color;
}

.post {
    color: $text_color;
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
    font-size: 13px;
    line-height: 1.5;
    min-width: 500px;
    overflow: visible;
}

.current {
    border-radius: 0;
    border-bottom: 2px solid lightgray;
}

.post h1 {
    text-align: left;
    font-size: 2.0em;
    margin-bottom: 32px;
}

.post h2 {
    font-size: 1.5em;
    &:before {
        content: "#";
        color: lightgray;
        position: absolute;
        padding-right: 4px;
        left: -1em;
    }
}

.post h3 {
  font-size: 1.2em;
  &:before {
      content: "##";
      color: lightgray;
      position: absolute;
      padding-right: 4px;
      left: -2em;
  }
}

.post h4 {
  font-size: 1.10em;
  line-height: 1.2;
  &:before {
      content: "###";
      color: lightgray;
      position: absolute;
      padding-right: 4px;
      left: -3em;
  }
}

.post h5 {
  font-size: 1em;
  &:before {
      content: "#### ";
      color: lightgray;
      position: absolute;
      padding-right: 8px;
      left: -1em;
  }
}

.post h6 {
  font-size: 1em;
  color: #777;
}

code {
    border-radius: $corner_radius;
    -moz-border-radius: $corner_radius;
    -webkit-border-radius: $corner_radius; 
    border: 1px solid teal;
    padding: 0.1em 0.4em;
    margin: 0;
    white-space: break-spaces;
    font: 12px Consolas, Monaco, "Andale  Mono","DejaVu Sans Mono",monospace;
    font-size: 85%;
    line-height: 1.5;
    color: $text_color;
}

table{
    color: #333; /* Lighten up font color */
    font-family: Helvetica, Arial, sans-serif; /* Nicer font */
    width: 100%;
    border-collapse:
    collapse;
    border-spacing: 1px;
}

table td, th { border: 1px solid #CCC; height: 30px; } /* Make cells a bit taller */

table th {
    background: #F3F3F3; /* Light grey background */
    font-weight: bold; /* Make sure they're bold */
}

table td {
    background: #FAFAFA; /* Lighter grey background */
    text-align: center; /* Center our text */
}

.asciiart {
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    border: 1px solid #BCBEC0;
    padding: 12px;
    font:12px Consolas, Monaco, "Andale  Mono","DejaVu Sans Mono",monospace;
    line-height: 1;
    color: teal;
}

blockquote {
    position: relative;
    padding-left: 1em;
    border-left: 0.2em solid teal;
    font:16px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    font-weight: 100;
    &:before, &:after {
        content: '\201C';
        font-family: 'sans-serif';
        color: teal;
    }
    &:after { content: '\201D'; }
}

.menu_item{
    margin-left: 18px;
    margin-bottom: 18px;
    font-size: 1.2em;
    line-height: 1.0;
    font-family: Monaco, Consolas, "Andale  Mono","DejaVu Sans Mono", monospace;
}

.center{
    top: 5%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
    overflow: visible;
}

.two_column{
    display: flex;
    font-size: 12px;
}

.fig_left {
  width: 45%;
  margin-right: 5%;
}

.fig_right {
  width: 45%;
  margin-left: 5%;
}

.intro {
    font-size: 14px;
    line-height: 1.5;
    padding: 12px;
    text-align: center;
    color: $text_color;
    font-family: monospace;
    border-bottom: 1px solid #BCBEC0;
}

.links{
    font-size: 18px;
    line-height: 1.732;
    padding: 12px;
    text-align: left;
    color: $text_color;
    font-family: monospace;
}

img {
    padding: 5%;
    text-align: center;
    width: 60%;
}

#side_bar{
    color: $text_color;
    top: 10%;
    position: fixed;
    overflow: auto;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 4px;
    max-height: 85vh;
    max-width: 20%;
}

#side_bar::-webkit-scrollbar {
    width: 7px;
}

#side_bar::-webkit-scrollbar-track {
    border-radius: 5px;
    background-color: #e7e7e7;
    border: 1px solid #cacaca;
}

#side_bar::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color:  #c0c0c0;
}

#side_bar li{
    list-style-type: none;
    margin-top: 8px;
    margin-bottom: 8px;
}

#side_bar ul{
    padding-left: 14px;
    margin-bottom: 8px;
    list-style-type: none;
}

#markdown-toc-new{
    overflow: auto;
}

.cn_label {
    text-align: center;
    font:18px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    color: crimson;
}

.en_label {
    text-align: center;
    font:18px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    color: navy;
}

.sci_label {
    float: left;
    width: 48px;
    text-align: center;
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    border: 1px solid #BCBEC0;
    padding: 2px 2px 2px 2px;
    font:10px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    margin-right: 10px;
    color: indigo;
}

.code_label {
    float: left;
    width: 48px;
    text-align: center;
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    border: 1px solid #BCBEC0;
    padding: 2px 2px 2px 2px;
    font:10px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    margin-right: 10px;
    color: seagreen;
}

.idea_label {
    float: left;
    width: 48px;
    text-align: center;
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    border: 1px solid #BCBEC0;
    padding: 2px 2px 2px 2px;
    font:10px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    margin-right: 10px;
    color: tomato;
}

.fold_click {
    font:14px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    text-align: center;
    margin-left: 2px;
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    color: $menu_text_color;
}

.fold_click:active, .fold_click:hover {
    color: tomato;
    outline: 1px;
}

.toc_click{
    border-left: 2px solid #BCBEC0;
    font:14px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    text-align: left;
    padding: 4px 24px 4px 24px;
    overflow: auto;
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    color: $menu_text_color;
}

.toc_click:active, .toc_click:hover {
    color: tomato;
    outline: 1px;
}

.footnotes{
    border-top: 2px solid #BCBEC0;
    margin-top: 5%;
}

.query{
    min-width: 250px;
    margin-bottom: 2em;
}

#search {
    padding: 10px 15px;
    border: 2px solid teal;
    border-radius: 4px;
    transition: box-shadow 0.3s ease;
    width: 100%;
}

#search:focus {
    outline: none;
    box-shadow: 0px 0px 10px rgba(100, 100, 100, 0.1);
}

#search::placeholder {
    color: #999;
}

#searchResults a {
    display: block;
    padding: 5px 10px;
    color: #333;
    text-decoration: none;
}

#searchResults a:hover {
    background-color: #f5f5f5;
}